<template>
   <div class="content">
		<div class="title">
		<img class="imgtit" src="../../static/img/Firefighting_jinggao2.png"/>
			<span class="titlespan">消防栓和消防泵告警</span>
			
		</div>
		
       <div class="container" style="">
		   <img  class="imglogo imglogo1" src="../../static/img/Firefighting_shuiya.png"/>
		   <img  class="imglogo" src="../../static/img/Firefighting_loushui.png"/>
		   <img  class="imglogo" src="../../static/img/Firefighting_shuimen.png"/>
	   
		   <div class="valuediv1">
			   <span class="getvalue getvalue1">消防栓</span>
			   <div class="imgline1 imggreen" ></div>			
			   <div class="imgline1 imgred" ></div>			
			   <div class="imgline1 imgblue" ></div>	    
		    </div>
		   <div class="valuediv2">
			   <span class="getvalue getvalue2">消防泵</span>
			   <div class="imgline1 imggreen imggreen2" ></div>			
			   <div class="imgline1 imgred imgred2" ></div>			
			   <div class="imgline1 imgblue imgblue2" ></div>	    
		    </div>	   
		   <div class="valuediv3">
			   <span  class="text text1">水压</span>
			   <span  class="text text2">漏水</span>
			   <span class="text text3">水门位置</span>
		   </div>
		   
	   </div>
	  </div>
</template>

<script>

import {getPerc} from '@/assets/js/assets.com.js';

export default {
	name: '',
	props: {
		list: {
			default(){
				return [
					{
						"power": "11%",
						"drain": "23%",
						"dall": "44%"
					},
					{
						"power": "52%",
						"drain": "11%",
						"dall": "12%"
					}
				]
			}
		}
	},
	data () {
		return {
		  
		}
	},
	mounted(){
		this.percent();
	},
	watch: {
		list(){
			this.percent();
		}
	},
	methods: {
		percent(){
			var arr = [this.list[0].power, this.list[0].drain, this.list[0].dall ];
			var arr2 = [this.list[1].power, this.list[1].drain, this.list[1].dall ];

			$( this.$el ).find('.valuediv1 .imgline1').each(function (index, ele) {
				$(ele).css({
					'background-size': arr[index]+' 110%'
				})
			});
			$( this.$el ).find('.valuediv2 .imgline1').each(function (index, ele) {
				$(ele).css({
					'background-size': arr2[index]+' 110%'
				})
			});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.content{
		margin:0;
		border:1px solid #113355;
		height: 430px;
		width:1140px;
	}
	
	.title{			
		font-size:30px;
		margin-top:25px;
		color:#00ffee;
		margin-left:22px;			
	}
	.imgtit{
		float:left;
		margin-top:-10px;
		margin-right:21px;
		width:32px;
		height:49px;
	}
	
	.container{		
		height:210px;			
		margin-top:35px;
	}
	
	.valuediv2{
		margin-top:-25px;
	}		
	.imglogo{		
		margin-left:187px;
	}
	.imglogo1{		
		margin-left:201px;
	}		
	
	
	.getvalue{		
		font-size:24px;
		float:left;			
		margin-left:102px;
		width:100px;
		text-align:center;
		font-family: MicrosoftYaHei;
		color: #00fff0;
	}
	
	.imgline1{
		height:30px;
		width:117px;			
		background-color: rgba(149, 149, 149, 0.21);
		border-radius: 10px;
		border: solid 1px rgba(255, 255, 255, 0.21);			
	}
	.imggreen{		
		margin-left:205px;
		margin-top:35px;
		background:url(../../static/img/Firefighting_greenBar.png) no-repeat ;
		background-size:70% 110%;
	}
	.imgred{
		margin-left:522px;
		margin-top:-30px;
		background:url(../../static/img/Firefighting_redBar.png) no-repeat ;
		background-size:20% 110%;
		
	}
	.imgblue{	
		margin-left:839px;
		margin-top:-30px;
		background:url(../../static/img/Firefighting_blueBar.png) no-repeat ;
		background-size:50% 110%;			
	}
	
	.imggreen2{				
		background-size:50% 110%;
	}
	.imgred2{		
		background-size:60% 110%;
	}
	.imgblue2{				
		background-size:25% 110%;			
	}
	
	.valuediv3{
		width: 100%;
		text-align: left;
		white-space: nowrap;
		overflow: hidden;
	}
	.text{
		color: #00fff0;
		font-size:30px;
		float:left;
		text-align:center;		
		width:120px;
		margin-top:10px;
	}
	.text1{
		margin-left:192px;
	}
	.text2{
		margin-left:207px;
	}
	.text3{
		margin-left:200px;
	}
</style>


