export default function (dom, myData, outOpt) {
	outOpt = outOpt || {}
	var myChart;
	if ($(dom).find('canvas').length > 0) {
		myChart = echarts.getInstanceByDom(dom)
	} else {
		myChart = echarts.init(dom);
	}

	var option = null;
	option = {
		title: {
			x: 'center'
		},
		tooltip: {
			trigger: 'item',
			formatter: "{b} : {c} ({d}%)"
		},
		grid: {
			bottom: '5%',
			top: '5%',
			left: '5%',
			right: '5%'
		},
		legend: {
			top: '30px',
			left: '410px',
			icon: 'circle',
			padding: [5, 5],
			itemGap: 35,
			itemWidth: 20,
			itemHeight: 20,
			borderRadius: 20,

			// data: ['设备一', '设备二', '设备三', '设备四', '设备五', '设备六'],

			textStyle: {
				color: '#ffffff',
				fontSize: 15,
			},
		},
		series: [{
			name: '设备分类',
			type: 'pie',
			radius: '75%',
			center: ['28%', '50%'],
			data: myData,
			label: {
				normal: {
					show: false,
					position: 'inside'
				}
			},
			itemStyle: {
				emphasis: {
					shadowBlur: 10,
					shadowOffsetX: 0,
					shadowColor: 'rgba(0, 0, 0, 0.5)'
				}
			}
		}]
	};
	if (option && typeof option === "object") {
		myChart.setOption(option, true);
		myChart.setOption(outOpt, false);
	}
}


