<!-- 消防安全 -->
<template>
	<page-box>
		<!-- 左边 -->
		<div class="c_left clear" slot="left">
			<div class="left_1 clear">
				<todayWarning 
					:static="static.left0101"
				></todayWarning>
				<deploy
					:static="static.left0102"
				></deploy>
			</div>
			<div class="left_2">
				<div class="left_2_1">
					<warn-sum 
						:num="static.left02.num"
						:static="static.left02"
					></warn-sum>
				</div>
				<table-t01 class="left_2_2"
					:tdList="static.left02.tdList"
					:static="static.left02"
				></table-t01>
			</div>
			<div class="left_3">
				<div class="left_3_1">
					<div class="com_title">{{static.left0301.title}}</div>
					<div class="cont flex">
						<div class="flex_item" v-for="item in static.left0301.list">
							<p><img :src="'./static/img/'+item.img"></p>
							<p class="num">{{item.num}}</p>
							<p class="desc">{{item.desc}}</p>
						</div>
					</div>
				</div>
				<div class="left_3_2">
					<table-order-day
						:static="static.left0302"
					></table-order-day>
				</div>
			</div>
		</div>
		<!-- 中间 -->
		<div class="c_center clear" slot="center">
			<div class="center_1">
				<iframe :src="static.centerTop01.iframe" class="cont"></iframe>
			</div>
			<div class="center_2">
				<warn-area-bar
					:static="static.center02"
					:list="static.center02.dataList"
				></warn-area-bar>
			</div>
			<div class="center_3">
				<div class="com_title">{{static.center03.title}}</div>
				<div class="chart"></div>
			</div>
		</div>
		<!-- 右边 -->
		<div class="c_right clear" slot="right">
			<div class="right_1">
				<div class="com_title">{{static.right01.title}}</div>
				<div class="cont">
					<table class="table">
						<tbody>
							<tr>
								<th v-for="item in static.right01.thList">{{item}}</th>
							</tr>
							<tr v-for="(item, index) in static.right01.tdList">
								<td :class="orderClass(index)">{{ item.col_1 }}</td>
								<td>{{ item.col_2 }}</td>
								<td>{{ item.col_3 }}</td>
								<td :class>{{ item.col_4 }} <i :class="item.hb"></i></td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
			<div class="right_2">
				<warn-occur-time
					:list="static.right02.dataList"
					:static="static.right02"
				></warn-occur-time>
			</div>
			<div class="right_3">
				<fire-right03
					:list="static.right03.list"
					:static="static.right03"
				></fire-right03>
			</div>
		</div>
	</page-box>
</template>

<script>

import PageBox from '@/modules/page-box.vue';

import todayWarning from '@/components/todayWarning.vue';
import deploy from '@/components/deploy.vue';
import tableOrderDay from '@/components/tableOrderDay.vue';

// 消防告警
import warnSum from '@/components/warnSum.vue';
import tableT01 from '@/components/tableT01.vue';
import warnToday from '@/components/warnToday.vue';

import warnAreaBar from '@/components/warnAreaBar.vue';
import warnOccurTime from '@/components/warnOccurTime.vue';
import fireRight03 from '@/components/fireRight03.vue';

// 静态数据
import pageJson from "@/../static/pageJson/fire.json";

// echart
import initPie01 from '@/charts/pie01.js';

// js
import {insertScript} from '@/assets/js/assets.com.js';

export default {
	name: '',
	components: {
		PageBox, 
		todayWarning, deploy, warnSum, tableT01, warnToday, tableOrderDay,
		warnAreaBar,
		warnOccurTime, fireRight03
	}, 
	data () {
		return {
		  static: pageJson,
		  timer: '' // 计时器
		}
	},
	mounted(){
		var timerI = 0;
		var _vue = this;

		this.queryData(timerI);

		this.timer = setInterval(function () {
			console.log('fire:', timerI )
		  
			_vue.queryData(timerI);
		  timerI++;
		  timerI%=5;
		}, 30000);
	},
	beforeRouteLeave (to, from, next){
		clearInterval( this.timer );
		next();
	},
	methods: {
		orderClass(ind){
			if ( ind <= 2 ) {
				return 'order_'+(ind+1);
			}else{
				return 'order';
			}
		},
		updata(){
			// 更新数据的操作放着里
			// 组件更新为监听数据变化
			var list =  this.static.center03.dataList.map(function(item) {
				return {
					value: item.num_alert,
					name: item.district_name 
				};
			});
			initPie01( this.$el.querySelector('.center_3 .chart'), list )
		},
		queryData(index){
			var _vue = this;
			
			insertScript( `./static/data/fire/data${index}.js` );
			// $('#myScript').html( `'<script data-type=static src=""><\/script>'` );
			
			_vue.static = $.extend(true, {}, pageJson, staticData ) ;
			_vue.updata();
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	@import url(../../../assets/less/var.less);
	div{
		box-sizing: border-box;
	}
	// 坐边
	.c_left{
		padding-left: 40px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.left_all(){
			overflow: hidden;
			box-sizing: border-box;
		}
		.left_1{
			.left_all();
			width: 100%;
			height: 265px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.left_2{
			.left_all();
			width: 400px;
			height: 675px;
			border: solid 1px rgba(56, 174, 225, 0.71);
			border-radius: 8px;
			.left_2_2{
				height: 415px;
				border: 1px solid #054453;
				border-radius: 10px;
			}
		}
		.left_3{
			.left_all();
			width: 800px;
			padding-left: 15px;
			height: 680px;
		}
		.left_3_1, .left_3_2{
			box-sizing: border-box;
			width: 100%;
			height: 330px;
			margin-bottom: 15px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
		}
	}
	// 中间
	.c_center{
		padding: 0 20px 0 20px;
		>div{
			float: left;
		}
		.center_1{
			width: 1390px;
			height: 600px;
			margin-bottom: 20px;
			border-radius: 8px;
			// border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.center_2{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
			margin-right: 10px;
		}
		.center_3{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
		}
	}
	// 右边
	.c_right{
		height: 100%;
		width: 100%;
		padding-right: 30px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.right_1{
			width: 570px;
			height: 510px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
		.right_2{
			width: 555px;
			height: 510px;
			margin-bottom: 15px;
			margin-left: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
		.right_3{
			width: 1140px;
			height: 430px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
	}

	// 左边内容
	.left_1{
		>div{
			float: left;
		}
	}
	.left_2_2{

	}
	.left_3_1{
		.cont{
			width: 100%;
			height: 210px;
			padding-top: 40px;
			justify-content: space-around;
			text-align: center;
			font-size: 24px;
			img{
				height: 100px;
			}
			.num{
				font-size: 40px;
			}
			.desc{
				color: #eaeaea;
			}
		}

	}
	.center_2{
		padding-right: 10px;
		overflow: hidden;
	}
	.com_title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 40px;
	}

	// 中间
	.center_1{
		position: relative;
		img{
			position: absolute;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
		}
	}
	.center_3{
		background-color: rgba(4, 34, 65, 0.5);
		.chart{
			height: 260px;
			padding-top: 20px;
		}
	}
	.right_1{
		.table{
			.mytable01();
			th{
				color: #eaeaea;
				font-size: 16px;
			}
			td{
				padding: 16px 0;
				position: relative;
				font-size: 14px;
				.order(@url){
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					background: url(@url) no-repeat center;
					background-size: 35px;
				}
				.order_bg(){
					content: "";
					position: absolute;
					top:50%;
					left:50%;
					width: 40px;
					height: 40px;
					transform: translateX(-50%) translateY(-50%);
					border-radius: 50%;
					background-color: #fff;
					z-index: -1;
				}
				&.order_1::after{
					// .order('../../../../static/img/order_1.png');
				}
				&.order_2::after{
					// .order('../../../../static/img/order_2.png');
				}
				&.order_3::after{
					// .order('../../../../static/img/order_3.png');
				}
				&.order{
					// color: #888;
				}
				&.order::after{
					// .order_bg();
				}
			}
		}
	}

</style>


